import ProjectList from "./components/ProjectList";
import ProjectStats from "./components/ProjectStats";
import {
    AppBar,
    createTheme,
    CssBaseline,
    Grid, Hidden,
    ThemeProvider,
    Toolbar, Typography
} from "@mui/material";
import {useEffect, useState} from "react";
import {
    HashRouter as Router,
    Routes,
    Route,
} from "react-router-dom";

import Logo from './images/logo_small.png';
import axios from "axios";

const theme = createTheme({
    palette: {
        primary: {
            main: '#872eff'
        }
    }
});

function DivvyDashboard() {

    const [projects, setProjects] = useState([]);

    useEffect(() => {
        axios
            .get('https://raw.githubusercontent.com/DivvySD/dashboard/main/projects.json')
            .then(resp => {
                setProjects(resp.data);
            })
    }, []);

    if (!projects.length) return <></>;

    return (
        <ThemeProvider theme={theme}>
            <CssBaseline/>
            <AppBar color="primary">
                <Toolbar>
                    <img src={Logo} height={32}/>
                </Toolbar>
            </AppBar>
            <Hidden mdUp>
                <Grid container style={{marginTop:80}}>
                    <Grid item xs={12} style={{padding:20}}>
                        <Typography>Divvy Dashboard is currently built for a desktop experience. We will be working on a mobile version soon.</Typography>
                    </Grid>
                </Grid>
            </Hidden>
            <Hidden mdDown>
                <Router>
                    <Grid container style={{marginTop: 64}}>
                        <Grid item style={{
                            width: '25%',
                            height: 'calc(100% - 64px)',
                            overflow: 'auto',
                            position: 'fixed',
                            borderRight: '1px solid #eee'
                        }}>
                            <ProjectList projects={projects} />
                        </Grid>

                        <Grid item xs={9} style={{
                            left: '25%',
                            height: 'calc(100% - 64px)',
                            overflow: 'auto',
                            padding: 40,
                            position: 'fixed',
                            backgroundColor: '#fafafa'
                        }}>
                            <Routes>
                                <Route
                                    path={`/`}
                                    exact={true}
                                    element={<ProjectStats project={projects[0]} />}
                                />
                                {
                                    projects.map((project, i) => {
                                        return <Route
                                            key={i}
                                            path={`/${project.symbol}`}
                                            exact={true}
                                            element={<ProjectStats project={project} />}
                                        />
                                    })
                                }
                            </Routes>
                        </Grid>
                    </Grid>
                </Router>
            </Hidden>
        </ThemeProvider>
    );
}

export default DivvyDashboard;
